@import "style/mixins/breakpoints";
.campanha {
  display: flex;
  justify-content: center;
  .campanha-container {
    width: 90%;
    .descricao-box {
      margin: 4.6% 0 13% 0;
      display: flex;
      flex-wrap: wrap;
      .descricao-titulo {
        width: 50%;
        .titulo-box {
          h1.titulo-campanha {
            color: #323232;
            font-family: "Druk Text Wide Super";
            text-transform: uppercase;
            font-size: 4vw;
            letter-spacing: 0.5vw;
            margin-left: -0.5%;
            line-height: 1.1;
            margin-top: -0.5%;
            margin-bottom: 1%;
          }
          p {
            color: #323232;
            font-family: "Druk Text Wide Medium";
            text-transform: uppercase;
            margin-bottom: 1%;
            font-size: 1vw;
            letter-spacing: 0.1vw;
            @include media-breakpoint-down(xs) {
              margin-bottom: -0.5%;
            }
            // &.cliente {
            // }
            // &.agencia {
            // }
            // &.funcao {
            // }
          }
        }
      }
      .descricao-texto {
        width: 50%;
        display: flex;
        justify-content: flex-end;
        .paragrafo-box {
          width: max-content;
          p {
            font-size: 1vw;
            color: #323232;
            line-height: 1.7;
            font-family: "Druk Text Wide Medium";
            width: max-content;
          }
          p + p {
            margin-top: 4%;
          }
        }
      }
    }
    .imagens {
      display: flex;
      flex-direction: row;
      align-items: center;
      flex-wrap: wrap;
      img {
        width: 97.5%;
        &.img-boticario-1 {
          width: 48.75%;
        }
        &.img-boticario-2 {
          width: 48.75%;
        }
        &.img-boticario-3 {
          width: 48.75%;
        }
        &.img-boticario-4 {
          width: 48.75%;
        }
        &.img-wine-1 {
          margin-bottom: 1%;
        }
        &.img-wine-2 {
          margin-bottom: 1%;
        }
        &.img-wine-3 {
          margin-bottom: 1%;
        }
        &.img-wine-4 {
          margin-bottom: 1%;
        }
      }

      &.whiskas {
        img {
          margin-bottom: 1%;
          &.img9 {
            width: 31.83%;
            margin-right: 1%;
          }
          &.img10 {
            width: 31.83%;
            margin-right: 1%;
          }
          &.img11 {
            width: 31.83%;
          }
          &.img12 {
            width: 76.5%;
          }
          &.img13 {
            width: 76.5%;
            margin-left: 21%;
          }
          &.img14 {
            width: 76.5%;
          }
        }
      }
    }
  }
}

.video-responsive {
  position: relative;
  display: block;
  width: 97.5%;
  overflow: hidden;
}

.video-responsive.whiskas {
  margin-bottom: 1%;
}

.video-responsive::before {
  display: block;
  content: "";
  padding-top: 56.25%;
}

.video-responsive.whiskas::before {
  display: block;
  content: "";
  padding-top: 67.25%;
}

.video-responsive-item {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}
