@import "style/mixins/browser-wrapper";

@include safari() {
}

.about-me {
  display: flex;
  justify-content: center;
  .about-me-container {
    width: 90%;
    .content {
      .profile-image {
        display: flex;
        justify-content: center;
        margin-bottom: 8%;
        .image {
          width: 63%;
          height: 100%;
        }
        &::before {
          content: "  ";
          background-repeat: no-repeat;
          background-size: 100%;
          background-image: url("../../assets/about-me/svg/about-me.svg");
          width: 90.5%;
          position: absolute;
          height: 20%;
          top: 41vw;
        }
      }
      .alignment {
        display: flex;
        justify-content: center;
        margin-left: 6%;
        .description {
          font-family: "Druk Text Wide Super";
          text-transform: uppercase;
          transform: scale(1, 0.95);
          // letter-spacing: 0.35rem;
          color: #323232;
          line-height: 1.8;
          margin-bottom: 12%;
          // font-size: 1.4rem;
          font-size: 1.146vw;
          letter-spacing: 0.3vw;
          // p:nth-child(1) {
          // }
          p:nth-child(2) {
            margin-bottom: 18%;
            font-family: "Druk Text Wide Medium";
          }
          p:nth-child(3) {
            margin-bottom: 4%;
            font-family: "Druk Text Wide Medium";
          }
          p:nth-child(4) {
            margin-bottom: 4%;
          }
          p:nth-child(5) {
            margin-bottom: 4%;
          }
          p:nth-child(6) {
            margin-bottom: 18%;
          }
          p:nth-child(7) {
            margin-bottom: 4%;
          }
          p:nth-child(8) {
            margin-bottom: 4.5%;
            font-family: "Druk Text Wide Medium";
          }
          .links-social-networks {
            display: flex;
            width: 100%;
            height: 6%;
            .icon {
              width: 9%;
              display: flex;
              transform: scale(0.95, 1);
              margin-left: -0.3%;
              &::before {
                display: inline-block;
                width: 100%;
                height: 100%;
                content: " ";
                background-repeat: no-repeat;
                background-size: 100%;
              }
            }
            .icon + .icon {
              margin-left: 2%;
            }
            .linked-in {
              &::before {
                background-image: url("../../assets/about-me/svg/linkedin.svg");
              }
              span{
                display: none;
              }
            }
            .instagram {
              &::before {
                background-image: url("../../assets/about-me/svg/insta.svg");
              }
              span{
                display: none;
              }
            }
            .facebook {
              &::before {
                background-image: url("../../assets/about-me/svg/face.svg");
              }
              span{
                display: none;
              }
            }
          }
        }
      }
    }
  }
}
