.controls {
  display: flex;
  flex-direction: row;
  margin-bottom: 3%;
  width: 100%;
  justify-content: center;
  align-items: center;
  div {
    width: 7%;
    margin: 0 2%;
  }
  a {
    font-size: 0.9vw;
    font-family: "Druk Text Wide Medium";
    text-transform: uppercase;
    color: #323232;
    letter-spacing: 0.28vw;
    text-decoration: none;
    width: max-content;
    display: flex;
  }
}
