@import "style/mixins/breakpoints";
// @import "style/debug/debug";

header.header {
  width: 100%;
  // min-height: 60vh;
  margin-top: 10.5%;
  @include media-breakpoint-up(xs) {
  }
  // @include media-breakpoint-up(xs) {
  // }
  // @include media-breakpoint-up(md) {
  // }
  // @include media-breakpoint-up(lg) {
  // }
  .main {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: column;
    .hi {
      display: flex;
      // justify-content: center;
      width: 100%;

      margin-bottom: 4.6%;
      padding-left: 42.9%;
      svg.hi-svg {
        width: 17%;
        height: 100%;
      }
    }
    .name {
      width: 100%;
      svg.fernando {
      }
      margin-bottom: 1%;
      @include media-breakpoint-down(xs) {
        margin-bottom: 0;
      }
    }
    .last-name-about {
      display: flex;
      width: 100%;
      margin-bottom: 4.6%;
      .last-name {
        width: 125%;
        svg.fregate {
        }
      }
      .about {
        width: 25%;
        .me {
          width: 100%;
          display: flex;
          align-items: center;
          height: 100%;
          justify-content: space-between;
          padding-left: 15%;
          padding-bottom: 4%;
          @include media-breakpoint-down(xs) {
            margin-top: -2%;
          }
          svg.plus {
            width: 18%;
          }
          svg.about-me {
            width: 70%;
          }
        }
      }
    }
    .description-box {
      width: 100%;
      div.creative-director {
        padding-left: 42.9%;
        width: 63%;
        margin-bottom: 3%;
        svg.creative-director {
          // background-color: red;
        }
      }
      div.currently {
        padding-left: 42.9%;
        width: 64.8%;
        margin-bottom: 10.5%;
        @include media-breakpoint-down(xs) {
          margin-bottom: 9%;
        }
        svg.currently {
          // background-color: red;
        }
      }
    }
  }
}
