
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  background: #e4e4e4;
  -webkit-font-smoothing: antialiased;
  height: 100%;
  min-height: 100%;
  min-width: 100%;
  overflow-x: hidden;

}

.App{
  max-width: 100%;
}


@font-face {
  font-family: "Druk Text Wide Super";
  src: url("assets/font/DrukTextWide-Super.woff") format("woff"), url("assets/font/DrukTextWide-Super.ttf")  format("truetype");
}
@font-face {
  font-family: "Druk Text Wide Medium";
  src: url("assets/font/DrukTextWide-Medium.woff") format("woff"), url("assets/font/DrukTextWide-Medium.ttf")  format("truetype");
}




