@include safari() {

}

.home {
  display: flex;
  justify-content: center;
  .container-home {
    width: 90%;
    .description {
      opacity: 1;
      position: relative;
      z-index: 10;
      display: flex;
      flex-direction: column;
      align-items: center;
      transition: all 500ms;
      -webkit-transition: all 500ms;
      width: 100%;
      top: 45%;
      cursor: pointer;
      svg {
        width: 100%;
        min-height: 10%;
        margin-bottom: 5px;
      }
      h3 {
        color: #fff;
        font-family: "Roboto", sans-serif;
        letter-spacing: 10px;
        font-size: 40px;
      }
      p {
        color: #fff;
        font-family: "Roboto", sans-serif;
        letter-spacing: 10px;
        font-size: 40px;
      }

      &.show {
        opacity: 1;
      }
    }
    .campanha {
      margin-bottom: 3%;
      overflow: hidden;
      &:hover {
        img {
          transform: scale(1.1);
        }
      }

      a {
        display: flex;
        transition: all 500ms;
        -webkit-transition: all 500ms;
        img {
          transition: all 500ms;
          -webkit-transition: all 500ms;
          transform: scale(1);
          width: 100%;
          height: 100%;
          opacity: 0.9;
        }
        &::before {
          opacity: 0;
          position: absolute;
          content: "  ";
          background-repeat: no-repeat;
          background-size: 100%;
          height: 20%;
          z-index: 10;
          margin-top: 15.2%;
          transition: all 500ms;
          -webkit-transition: all 500ms;
          @include safari() {
            width: 90%;
            margin-top: 14%;
          }
        }
        &::after {
          content: "  ";
          transition: all 500ms;
          -webkit-transition: all 500ms;
          @include safari() {
            width: 90%;
            height: 120%;
          }
        }
        &:hover {
          filter: grayscale(100%);
          &::before {
            width: 100%;
            opacity: 1;
            filter: none;
            @include safari() {
              width: 90%;
            }
          }
          &::after {
            @include safari() {
              width: 90%;
              height: 120%;
            }
            width: 100%;
            position: absolute;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.1);
          }
        }

        &.whiskas::before {
          background-image: url("../../assets/home/svg/T1.svg");
        }
        &.boticario::before {
          background-image: url("../../assets/home/svg/T2.svg");
        }
        &.havaianas::before {
          background-image: url("../../assets/home/svg/T3.svg");
        }
        &.doritos::before {
          background-image: url("../../assets/home/svg/T4.svg");
        }
        &.burger-king::before {
          background-image: url("../../assets/home/svg/T5.svg");
        }
        &.estadao::before {
          background-image: url("../../assets/home/svg/T6.svg");
        }
        &.wine-labels::before {
          background-image: url("../../assets/home/svg/T7.svg");
        }
      }
    }
  }
}

